
.heading {
  text-align: center;
  text-transform: uppercase;
}

.heading__title {
  max-width: 400px;
  margin: 0 auto;
  margin-bottom: 24px;

  font-size: 18px;
  line-height: 26px;

  @media (min-width: $vp-768) {
    font-size: 30px;

    &--size-xs {
      font-size: 21px;
    }
  }

  &--size-xl {
    font-size: 21px;

    @media (min-width: $vp-768) {
      font-size: 30px;
    }
  }
}

.heading__description {
  max-width: 400px;
  margin: 0 auto;

  color: $color-night-rider;
}
